









import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src

@Component({
  components: {
    HelloWorld
  }
})
export default class Home extends Vue {
  hello : string = 'Hello World!'
  msg = 123
  $createDialog : any
  // 声明周期钩子
  mounted () {
    console.log('钩子')
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  get computedMsg () {
    return 'computed ' + this.hello + this.propB
  }
  showAlert () {
    this.$store.dispatch('login', { user: '13934691550', pass: 'wangtao' })
    this['$createDialog']({
      type: 'alert',
      title: '我是标题',
      content: '我是内容',
      icon: 'cubeic-alert'
    }).show()
  }
}
